export default class jsdate {
  //,timedatecFormatsting
  // this.ja_formatTime(new Date(),'{y}{M}{d} {h}:{m}:{s} {a}')
  static ja_formatTime(time, cFormat) {
    if (arguments.length === 0) return null
    if ((time + '').length === 10) {
      time = +time * 1000
    }

    var format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}',
      date
    if (typeof time === 'object') {
      date = time
    } else {
      date = new Date(time)
    }
    var formatObj = {
      y: date.getFullYear(), //
      M: date.getMonth() + 1, //
      d: date.getDate(), //
      h: date.getHours(), //
      m: date.getMinutes(), //
      s: date.getSeconds(), //
      a: date.getDay() //
    }
    var time_str = format.replace(/{(y|M|d|h|m|s|a)+}/g, (result, key) => {
      var value = formatObj[key]
      if (key === 'a') return ['', '', '', '', '', '', ''][value - 1]
      if (result.length > 0 && value < 10) {
        value = '0' + value
      }
      return value || 0
    })
    return time_str
  }
  /**
   *
   *
   * @static
   * @param {*} timestamp 
   * @returns
   * @memberof jsdate
   */
  static ja_formatTime2(timestamp) {
    var curTimestamp = parseInt(new Date().getTime() / 1000); //
    var timestampDiff = curTimestamp - timestamp; // 

    var curDate = new Date(curTimestamp * 1000); // 
    var tmDate = new Date(timestamp * 1000); // 

    var Y = tmDate.getFullYear(),
      m = tmDate.getMonth() + 1,
      d = tmDate.getDate();
    var H = tmDate.getHours(),
      i = tmDate.getMinutes(),
      s = tmDate.getSeconds();
    if (timestampDiff < 60) { // 
      return "";
    } else if (timestampDiff < 3600) { // 
      return Math.floor(timestampDiff / 60) + "";
    } else if (curDate.getFullYear() == Y && curDate.getMonth() + 1 == m && curDate.getDate() == d) {
      return '' + this.ja_zeroize(H) + ':' + this.ja_zeroize(i);
    } else {
      var newDate = new Date((curTimestamp - 86400) * 1000); // 
      if (newDate.getFullYear() == Y && newDate.getMonth() + 1 == m && newDate.getDate() == d) {
        return '' + this.ja_zeroize(H) + ':' + this.ja_zeroize(i);
      } else if (curDate.getFullYear() == Y) {
        return this.ja_zeroize(m) + '' + this.ja_zeroize(d) + ' ' + this.ja_zeroize(H) + ':' + this.ja_zeroize(i);
      } else {
        return Y + '' + this.ja_zeroize(m) + '' + this.ja_zeroize(d) + ' ' + this.ja_zeroize(H) + ':' + this.ja_zeroize(i);
      }
    }
  }
  /**
   *
   *
   * @static
   * @param {*} num
   * @returns
   * @memberof jsdate
   */
  static ja_zeroize(num) {
    return (String(num).length == 1 ? '0' : '') + num;
  }
  /**
   *
   *
   * @static
   * @param {*} interval 
   * @param {*} cFormat 
   * @param {*} destory 
   * @memberof jsdate
   */
  static ja_clock(interval,cFormat,destory){
    var clock;
    if(destory){
        clearInterval(interval);
    }else{
        interval=setInterval(()=>{
            clock=this.ja_formatTime(new Date() +1000,cFormat);
            return clock;
        },1000);
    }
  }
}



